"use client"

import type React from "react"

import { useState } from "react"
import { MoreVertical, Download, Trash2, ArrowUpDown, FileSignature } from "lucide-react"
import type { Document } from "@/types/document"
import { DocumentIcon } from "./document-icon"
import { StatusBadge } from "./status-badge"
import { Checkbox } from "@/components/ui/checkbox"
import { Button } from "@/components/ui/button"
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu"
import { cn } from "@/lib/utils"

interface DocumentTableProps {
  documents: Document[]
  selectedIds: string[]
  onSelectionChange: (ids: string[]) => void
  onDocumentClick: (document: Document) => void
  onSign: (document: Document) => void
}

type SortField = "name" | "status" | "athlete" | "competition" | "discipline"
type SortDirection = "asc" | "desc"

export function DocumentTable({
  documents,
  selectedIds,
  onSelectionChange,
  onDocumentClick,
  onSign,
}: DocumentTableProps) {
  const [sortField, setSortField] = useState<SortField>("name")
  const [sortDirection, setSortDirection] = useState<SortDirection>("asc")

  const handleSort = (field: SortField) => {
    if (sortField === field) {
      setSortDirection(sortDirection === "asc" ? "desc" : "asc")
    } else {
      setSortField(field)
      setSortDirection("asc")
    }
  }

  const sortedDocuments = [...documents].sort((a, b) => {
    let aValue: string, bValue: string
    switch (sortField) {
      case "name":
        aValue = a.name
        bValue = b.name
        break
      case "status":
        aValue = a.status
        bValue = b.status
        break
      case "athlete":
        aValue = a.athlete.fullName
        bValue = b.athlete.fullName
        break
      case "competition":
        aValue = a.competition
        bValue = b.competition
        break
      case "discipline":
        aValue = a.discipline
        bValue = b.discipline
        break
      default:
        return 0
    }
    const comparison = aValue.localeCompare(bValue, "ru")
    return sortDirection === "asc" ? comparison : -comparison
  })

  const handleSelectAll = () => {
    if (selectedIds.length === documents.length) {
      onSelectionChange([])
    } else {
      onSelectionChange(documents.map((d) => d.id))
    }
  }

  const handleSelectOne = (id: string) => {
    if (selectedIds.includes(id)) {
      onSelectionChange(selectedIds.filter((i) => i !== id))
    } else {
      onSelectionChange([...selectedIds, id])
    }
  }

  const SortButton = ({ field, children }: { field: SortField; children: React.ReactNode }) => (
    <button
      className="flex items-center gap-1 text-sm font-medium text-muted-foreground hover:text-foreground transition-colors"
      onClick={() => handleSort(field)}
    >
      {children}
      <ArrowUpDown className={cn("h-3.5 w-3.5", sortField === field && "text-foreground")} />
    </button>
  )

  return (
    <div className="rounded-lg border bg-white">
      {selectedIds.length > 0 && (
        <div className="flex items-center gap-4 px-4 py-3 border-b bg-accent/50">
          <div className="flex items-center gap-2">
            <Checkbox checked={selectedIds.length === documents.length} onCheckedChange={handleSelectAll} />
            <span className="text-sm font-medium">Выбрано: {selectedIds.length}</span>
          </div>
          <Button variant="ghost" size="sm" className="gap-2">
            <Download className="h-4 w-4" />
            Скачать выбранные
          </Button>
          <Button variant="ghost" size="sm" className="gap-2 text-destructive hover:text-destructive">
            <Trash2 className="h-4 w-4" />
            Удалить
          </Button>
        </div>
      )}

      <table className="w-full">
        <thead>
          <tr className="border-b">
            <th className="w-12 px-4 py-3">
              <Checkbox
                checked={selectedIds.length === documents.length && documents.length > 0}
                onCheckedChange={handleSelectAll}
              />
            </th>
            <th className="px-4 py-3 text-left">
              <SortButton field="name">Документ</SortButton>
            </th>
            <th className="px-4 py-3 text-left">
              <SortButton field="status">Статус</SortButton>
            </th>
            <th className="px-4 py-3 text-left">
              <SortButton field="athlete">Спортсмен</SortButton>
            </th>
            <th className="px-4 py-3 text-left">
              <SortButton field="competition">Соревнование</SortButton>
            </th>
            <th className="px-4 py-3 text-left">
              <SortButton field="discipline">Дисциплина</SortButton>
            </th>
            <th className="w-12 px-4 py-3"></th>
          </tr>
        </thead>
        <tbody>
          {sortedDocuments.map((doc) => (
            <tr
              key={doc.id}
              className={cn(
                "border-b last:border-b-0 hover:bg-accent/50 transition-colors cursor-pointer",
                selectedIds.includes(doc.id) && "bg-accent/30",
              )}
              onClick={() => onDocumentClick(doc)}
            >
              <td className="px-4 py-3" onClick={(e) => e.stopPropagation()}>
                <Checkbox checked={selectedIds.includes(doc.id)} onCheckedChange={() => handleSelectOne(doc.id)} />
              </td>
              <td className="px-4 py-3">
                <div className="flex items-center gap-3">
                  <DocumentIcon />
                  <div>
                    <div className="font-medium text-sm">{doc.name}</div>
                    <div className="text-xs text-muted-foreground">
                      {doc.type} {doc.date && `• ${doc.date}`} • {doc.size}
                    </div>
                  </div>
                </div>
              </td>
              <td className="px-4 py-3">
                <StatusBadge status={doc.status} />
              </td>
              <td className="px-4 py-3">
                <div className="flex items-center gap-2">
                  <span className="flex h-8 w-8 items-center justify-center rounded-full bg-orange-100 text-xs font-medium text-orange-700">
                    {doc.athlete.initials}
                  </span>
                  <span className="text-sm truncate max-w-[180px]">{doc.athlete.fullName}</span>
                </div>
              </td>
              <td className="px-4 py-3">
                <span className="text-sm">{doc.competition}</span>
              </td>
              <td className="px-4 py-3">
                <span className="text-sm">{doc.discipline}</span>
              </td>
              <td className="px-4 py-3" onClick={(e) => e.stopPropagation()}>
                <DropdownMenu>
                  <DropdownMenuTrigger asChild>
                    <Button variant="ghost" size="icon" className="h-8 w-8">
                      <MoreVertical className="h-4 w-4" />
                    </Button>
                  </DropdownMenuTrigger>
                  <DropdownMenuContent align="end">
                    {doc.status === "unsigned" && (
                      <DropdownMenuItem onClick={() => onSign(doc)}>
                        <FileSignature className="h-4 w-4 mr-2" />
                        Подписать
                      </DropdownMenuItem>
                    )}
                    <DropdownMenuItem>
                      <Download className="h-4 w-4 mr-2" />
                      Скачать
                    </DropdownMenuItem>
                    <DropdownMenuItem className="text-destructive">
                      <Trash2 className="h-4 w-4 mr-2" />
                      Удалить
                    </DropdownMenuItem>
                  </DropdownMenuContent>
                </DropdownMenu>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  )
}
